import {Howl, Howler} from 'howler';


var music = {
	sound: null,
	init()
	{
		this.sound = new Howl({
			src: ['musicBackground/0.mp3'],
			volume: 0.2,
			loop: true,
		});

		console.log(this.sound);
	},
	play()
	{
		console.log(this);
		this.sound.play();
	},

}
music.init();
music.play.bind(music);
